import { AnimeEmoji } from '../types/anime';

export interface AnimeConversationTopic {
  id: string;
  topic: string;
  prompt: string;
  followUpQuestions: string[];
  yumiReactions: string[];
  emoji: AnimeEmoji;
}

export interface OtakuPurityQuestion {
  id: string;
  question: string;
  context: string;
  yumiAnswer?: string;
  yumiReaction: string;
  category: 'classic' | 'modern' | 'meme' | 'culture';
  difficulty: 1 | 2 | 3;
}

export interface AnimeRecommendation {
  id: string;
  title: string;
  description: string;
  yumiThoughts: string;
  similarTo?: string[];
  tags: string[];
  imageUrl: string;
}

