import React, { useState } from 'react';
import './SenseiPanel.css';

interface JapaneseLesson {
  id: string;
  title: string;
  description: string;
  content: string;
  quiz: {
    question: string;
    options: string[];
    answer: number;
  }[];
}

const JAPANESE_LESSONS: JapaneseLesson[] = [
  {
    id: 'te-form',
    title: '-form ',
    description: '',
    content: `
      -form 
      
      1.   
      2.   
      3.   
      4.   
      5.   
    `,
    quiz: [
      {
        question: '-form ',
        options: ['', '', '', ''],
        answer: 1
      },
      {
        question: '-form ',
        options: ['', '', '', ''],
        answer: 2
      }
    ]
  },
  {
    id: 'kanji-basics',
    title: '',
    description: '',
    content: `
      
      1. 
      2. 
      3. 
    `,
    quiz: [
      {
        question: '',
        options: [
          '',
          '',
          '',
          ''
        ],
        answer: 0
      }
    ]
  }
];

export const SenseiPanel: React.FC = () => {
  const [selectedLesson, setSelectedLesson] = useState<string | null>(null);
  const [currentQuizIndex, setCurrentQuizIndex] = useState(0);
  const [showAnswer, setShowAnswer] = useState(false);
  const [selectedOption, setSelectedOption] = useState<number | null>(null);

  const lesson = selectedLesson 
    ? JAPANESE_LESSONS.find(l => l.id === selectedLesson)
    : null;

  const handleNextQuiz = () => {
    if (!lesson) return;
    if (currentQuizIndex < lesson.quiz.length - 1) {
      setCurrentQuizIndex(prev => prev + 1);
      setShowAnswer(false);
      setSelectedOption(null);
    }
  };

  const handleOptionSelect = (optionIndex: number) => {
    setSelectedOption(optionIndex);
    setShowAnswer(true);
  };

  return (
    <div className="sensei-panel">
      {!selectedLesson ? (
        <div className="lesson-list">
          <h3></h3>
          <div className="lesson-grid">
            {JAPANESE_LESSONS.map(lesson => (
              <div 
                key={lesson.id}
                className="lesson-card"
                onClick={() => setSelectedLesson(lesson.id)}
              >
                <h4>{lesson.title}</h4>
                <p>{lesson.description}</p>
              </div>
            ))}
          </div>
        </div>
      ) : lesson ? (
        <div className="lesson-content">
          <button 
            className="back-button"
            onClick={() => {
              setSelectedLesson(null);
              setCurrentQuizIndex(0);
              setShowAnswer(false);
            }}
          >
             
          </button>
          
          <h3>{lesson.title}</h3>
          <div className="lesson-explanation">
            <pre>{lesson.content}</pre>
          </div>

          <div className="quiz-section">
            <h4></h4>
            <div className="quiz-card">
              <p className="quiz-question">
                {lesson.quiz[currentQuizIndex].question}
              </p>
              <div className="quiz-options">
                {lesson.quiz[currentQuizIndex].options.map((option, i) => (
                  <button
                    key={i}
                    onClick={() => handleOptionSelect(i)}
                    className={`quiz-option ${
                      showAnswer
                        ? i === lesson.quiz[currentQuizIndex].answer
                          ? 'correct'
                          : selectedOption === i
                          ? 'incorrect'
                          : ''
                        : selectedOption === i
                        ? 'selected'
                        : ''
                    }`}
                    disabled={showAnswer}
                  >
                    {option}
                  </button>
                ))}
              </div>
              {showAnswer && (
                <div className="quiz-feedback">
                  {selectedOption === lesson.quiz[currentQuizIndex].answer ? (
                    <p className="correct-feedback"></p>
                  ) : (
                    <p className="incorrect-feedback">
                      
                      {lesson.quiz[currentQuizIndex].options[lesson.quiz[currentQuizIndex].answer]}
                      
                    </p>
                  )}
                  {currentQuizIndex < lesson.quiz.length - 1 && (
                    <button className="next-quiz-button" onClick={handleNextQuiz}>
                       
                    </button>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      ) : null}
    </div>
  );
}; 